@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
body {
  background-image: linear-gradient(154.8deg, #00b7ff, #67e1bf, #fff07c);
  height: 300vh;
  font-family: "Roboto", sans-serif;
  overflow-x: hidden;
}

main {
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  flex-flow: column nowrap;
}

.demoNav {
  margin: 10vmin;
  width: 10vmin;
  height: 10vmin;
  border: none;
  border-radius: 50%;
  cursor: pointer;
  background: rgb(187, 42, 42);
  color: white;
  transition-duration: 0.3s;
  font-weight: 900;
  font-size: 2.5em;
  text-align: center;
}

.demoNav:hover {
  background: gray;
  background: rgb(153, 37, 37);
}

.demoNav:active {
  background: rgb(118, 24, 24);
}

nav {
  display: flex;
  margin-top: 50px;
}

.navButton {
  margin: 5px;
  width: 20px;
  height: 20px;
  border: solid black 2px;
  border-radius: 50%;
  cursor: pointer;
}

section {
  display: inline-block;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 30px;
  box-shadow: 0 6px 10px 0 rgba(0, 0, 0, 0.14), 0 1px 18px 0 rgba(0, 0, 0, 0.12),
    0 3px 5px -1px rgba(0, 0, 0, 0.2);
  overflow: hidden;
  transition-duration: 0.5s;
  cursor: pointer;
}

.leave {
  -webkit-animation: goOut 1s ease-out;
          animation: goOut 1s ease-out;
}

.enter {
  -webkit-animation: goIn 1s ease-out;
          animation: goIn 1s ease-out;
}

@-webkit-keyframes goOut {
  0% {
    box-shadow: none;
    transform: none;
    opacity: 1;
  }
  40% {
    box-shadow: none;
    transform: none;
    opacity: 1;
  }
  100% {
    box-shadow: none;
    transform: translateX(-75vw) scale(0.8);
    opacity: 0;
  }
}

@keyframes goOut {
  0% {
    box-shadow: none;
    transform: none;
    opacity: 1;
  }
  40% {
    box-shadow: none;
    transform: none;
    opacity: 1;
  }
  100% {
    box-shadow: none;
    transform: translateX(-75vw) scale(0.8);
    opacity: 0;
  }
}

@-webkit-keyframes goIn {
  0% {
    box-shadow: none;
    transform: translateX(75vw) scale(0.8);
    opacity: 0;
  }
  60% {
    transform: none;
    box-shadow: none;
    opacity: 1;
  }
  100% {
    box-shadow: 0 6px 10px 0 rgba(0, 0, 0, 0.14),
      0 1px 18px 0 rgba(0, 0, 0, 0.12), 0 3px 5px -1px rgba(0, 0, 0, 0.2);
    transform: none;
    opacity: 1;
  }
}

@keyframes goIn {
  0% {
    box-shadow: none;
    transform: translateX(75vw) scale(0.8);
    opacity: 0;
  }
  60% {
    transform: none;
    box-shadow: none;
    opacity: 1;
  }
  100% {
    box-shadow: 0 6px 10px 0 rgba(0, 0, 0, 0.14),
      0 1px 18px 0 rgba(0, 0, 0, 0.12), 0 3px 5px -1px rgba(0, 0, 0, 0.2);
    transform: none;
    opacity: 1;
  }
}

