section:hover {
  box-shadow: 0 16px 24px 2px rgba(0, 0, 0, 0.14),
    0 6px 30px 5px rgba(0, 0, 0, 0.12), 0 8px 10px -5px rgba(0, 0, 0, 0.2);
  transition-duration: 0.5s;
}

section img {
  max-width: 50vmin;
  max-height: 50vmin;
  position: relative;
  -webkit-filter: blur(2px);
          filter: blur(2px);
}

article {
  -webkit-filter: none;
          filter: none;
  position: absolute;
  background-color: white;
  padding: 20px;
  border-radius: 5px;
  box-shadow: 0 4px 5px 0 rgba(0, 0, 0, 0.14), 0 1px 10px 0 rgba(0, 0, 0, 0.12),
    0 2px 4px -1px rgba(0, 0, 0, 0.2);
  max-width: 25vmin;
  opacity: 0.9;
}

a {
  color: black !important;
  outline: none;
}

header {
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

#profilePic {
  width: max(15vmin, 50px);
  border-radius: 50%;
  margin-right: 50px;
}

#aboutMe {
  display: flex;
  padding: 100px;
  flex-flow: row wrap;
  align-items: center;
  justify-content: center;
  font-size: 1.5em;
}

#meDescription {
  width: 50vmin;
  text-align: center;
}

